body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin:0px auto;
}

#root {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

main {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.App-link {
  color: #61dafb;
}

.button {
  margin: 25px 0;
}

select[type=text] {
  margin: 20px 0;
}

.form-label {
  margin: 20px 0;
}

.logo {
  width: 1.5%;
  height: auto;
}

.basic-form, .advanced-form {
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 30px 0 30px;
}

.advanced-form {
  padding: 10px 30px 20px 30px;
}

.brand-logo img {
  width: 13%;
  height: auto;
}

.navbar-title {
  padding-left: 10px;
  font-size: 2.5em;
}

.navbar-title, h4, nav, h5 {
  font-family: 'Indie Flower', cursive;
}

nav ul a {
  font-size: 20px !important;
}

td.no {
  color: red;
}

td.maybe {
  color: orange;
}

td.probably {
  color: #9abc03;
}

td.definitely {
  color: green;
}

.footer-built-by {
  padding-left: 6px;
}

.fa-github, .fa-twitter {
  margin-right: 10px;
}


@media only screen and (max-width: 600px) {
  .logo {
    width: 3.1%;
    height: auto;
  }
  .navbar-title {
    padding-left: 5px;
    font-size: 1.75em;
  }
}
